<template>
  <div>
    <div class="home">
      <!-- 轮播图 -->
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white" @change="changeSwipe">
        <van-swipe-item v-for="(item, index) in bannerData" :key="index">
          <img :src="item.bannerImg" alt />
        </van-swipe-item>
      </van-swipe>

      <!-- 内容页面 -->
      <div class="homeContain">
        <div class="hello">
          <div class="location"></div>
          <div class="text">
            <h1>Hi, {{userInfo.nickName}}</h1>
            <p>咖啡会让你脑洞打开哟</p>
          </div>
        </div>

        <!-- div -->
        <div class="way">
          <div class="left"></div>
          <div class="right"></div>
        </div>

        <div class="hot-products">
          <h2>热门推荐</h2>

          <div class="drop">
            <div class="product-box clearfix">
              <div
                class="product-item fl"
                :class="[index % 2 == 0 ? 'p-left' : 'p-right']"
                v-for="(item, index) in hotProducts"
                :key="index"
              >
                <div class="p-img" @click="viewProductInfo(item.pid)">
                  <img class="auto-img" :src="item.smallImg" alt />
                </div>
                <div class="pro-name one-text">{{item.name}}</div>
                <div class="clearfix">
                  <div class="pro-price fl">￥{{item.price}}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/home.less";
import { utils } from "../../assets/js/utils";

export default {
  name: "Home",

  data() {
    return {
      bannerData: [],
      hotProducts: [],
      userInfo: {
        nickName: "Jack",
      },

      isLogin: false,
    };
  },

  methods: {
    // post请求，参数放在data内
    // get请求，参数放在params内

    changeSwipe(index) {},

    // 获取轮播图数据
    getBannerData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      // get请求轮播图数据
      this.axios({
        method: "GET",
        url: "/banner",
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 300) {
            this.bannerData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 获取推荐商品数据
    getHotProducts() {
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 500) {
            this.hotProducts = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //查看商品详情
    viewProductInfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },

    //获取用户信息
    getUserInfo() {
      //获取token字符串
      let tokenString = localStorage.getItem("owner");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "A001") {
            if (result.data.result.length > 0) {
              this.isLogin = true;
              this.userInfo = result.data.result[0];
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },

  created() {
    this.getBannerData();
    this.getHotProducts();
    this.getUserInfo();
  },
};
</script>

<style lang="less" scoped>
</style>